.footer {
  .container {
    padding-top: 102px;
  }
}

.footer__header {
  margin-bottom: 22px;
  padding-right: 10px;

  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  text-align: center;

  color: $dark;

  &--corporate {
    margin-bottom: 20px;
    padding-right: 0;
    margin-right: -6px;
  }
}

.footer__place {
  margin-bottom: 10px;

  a {
    display: block;
    font-size: 14px;
    line-height: 17px;
    text-align: center;

    color: $grey-link;
    transition: 0.3s ease;

    &:hover {
      color: $dark;
    }
  }
}

.footer__next-trip-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.footer__next-trip-item {
  margin-bottom: 9px;
  width: 143px;

  &:last-child {
    a {
      margin-right: 10px;
    }
  }
}

.footer__next-trip-item--first {
  a {
    padding-right: 21px;
  }
}

.footer__contact {
  display: block;
  padding-left: 30px;
  
  text-align: center;
  text-decoration: underline;
  transition: 0.3s ease;

  color: $red;
  &--mail {
    margin-bottom: 19px;

    font-size: 14px;
    line-height: 17px;
  }

  &--tel {
    font-size: 16px;
    line-height: 32px;
  }

  &:hover {
    color: $dark;
  }

  &:active {
    opacity: 0.5;
  }
}

.footer__corporate-text {
  margin-right: -4px;

  font-size: 14px;
  line-height: 172%;
  text-align: center;

  color: $grey-link;
}

.footer__header--contacts {
  margin: 0;
  margin-bottom: 23px;
}

.footer__contacts {
  padding-top: 30px;
  padding-bottom: 15px;
}

.footer__corporate {
  padding-top: 11px;
  padding-bottom: 15px;
}

.footer__email {
  display: none;
}

@media ($tablet) {
  .footer {
    .container {
      padding-top: 14px;
      padding-left: 2.8%;
    }
  }

  .footer__wrapper {
    display: flex;
    padding-bottom: 28px;
  }

  .footer__header {
    text-align: left;
    margin-bottom: 21px;

    &--contacts {
      margin-bottom: 23px;
    }

    &--corporate {
      text-align: center;
      margin-right: 0;
      padding-right: 0;
      padding-left: 18px;
    }
  }

  .footer__next-trip {
    width: 55.9%;
    padding-right: 7.3%;
  }

  .footer__next-trip-list {
    justify-content: space-between;
  }

  .footer__next-trip-item {
    width: auto;
    margin-bottom: 0;

    &:last-child {
      a {
        margin-right: 0;
      }
    }
  }

  .footer__place {
    a {
      text-align: left;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  .footer__contacts {
    padding-top: 17px;
    padding-left: 6.9%;
  }

  .footer__contact {
    padding-left: 41px;

    text-align: left;
  }

  .footer__next-trip-item--first {
    a {
      padding-right: 0;
    }
  }

  .footer__next-trip-item--second {
    a {
      padding-right: 6px;
    }
  }

  .footer__corporate {
    padding-top: 12px;
    padding-bottom: 36px;
  }

  .footer__corporate-text {
    padding-left: 13px;
  }
}

@media ($desktop) {
  .footer {
    .container {
      display: flex;
      padding-top: 117px;
      padding-left: 20.2%;
      padding-right: 20%;
      padding-bottom: 24px;
    }
  }

  .footer__contacts {
    padding-left: 0;
  }

  .footer__wrapper {
    padding-right: 3.8%;
    width: 68%;
    margin-right: auto;
  }

  .footer__corporate {
    width: 28.6%;
    padding-top: 0;
  }

  .footer__corporate-text {
    padding-left: 8px;
    padding-right: 2px;

    text-align: left;
  }

  .footer__contacts {
    padding-top: 0;
  }

  .footer__header {
    margin: 0;
    margin-bottom: 21px;

    &--contacts {
      margin-bottom: 23px;
    }

    &--corporate {
      text-align: left;
      padding-left: 9px;
    }
  }

  .footer__next-trip {
    margin-right: auto;
    width: 47.3%;
    padding-right: 0;
  }

  .footer__next-trip-item--second {
    a {
      padding-right: 0;
    }
  }

  .footer__mail {
    display: none;
  }

  .footer__email {
    display: block;
  }
}